@font-face {
  font-family: 'Blogger Sans';
  src: url('../fonts/bloggersans.woff2') format('woff2'),
      url('../fonts/bloggersans.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Blogger Sans';
  src: url('../fonts/bloggersans-bold.woff2') format('woff2'),
      url('../fonts/bloggersans-bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
